import { defineComponent } from 'vue';
export default defineComponent({
    name: 'Button',
    props: {
        type: {
            type: String,
            required: false,
            default: 'button',
        },
        disabled: {
            type: Boolean,
            required: false,
            default: false,
        },
    },
    emits: ['click'],
    methods: {
        onClick(event) {
            if (!this.disabled) {
                this.$emit('click', event);
            }
        },
    },
});
