import { defineComponent } from 'vue';
export default defineComponent({
    name: 'InputField',
    props: {
        id: {
            type: String,
            required: true,
        },
        label: {
            type: String,
            required: true,
        },
        type: {
            type: String,
            default: 'text',
        },
        modelValue: {
            type: String,
            required: true,
        },
        placeholder: {
            type: String,
            required: true,
        },
        disabled: {
            type: Boolean,
            required: false,
            default: false,
        },
        maxlength: {
            type: Number,
            required: false,
        },
        minlength: {
            type: Number,
            required: false,
        },
        errorMessage: {
            type: String,
            required: false,
        },
    },
});
